<template>
	<Card>
		<p slot="title">
			{{title}}
		</p>
		<div slot="extra">
			<a href="#" @click="pageGoBack">
				<Icon type="android-arrow-back"></Icon> </a>
		</div>
		<Row>
			<Col span="12" offset="6">
			<Form ref="form" :model="form" :label-width="80" :rules="ruleValidate">
				<FormItem label="" prop="username">
					<Input v-model="form.username" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="password">
					<Input type="password" v-model="form.password" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="realname">
					<Input v-model="form.realname" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="phone">
					<Input v-model="form.phone" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="email">
					<Input v-model="form.email" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="sex">
					<RadioGroup v-model="form.sex">
						<Radio label="1"></Radio>
						<Radio label="0"></Radio>
					</RadioGroup>
				</FormItem>
				<FormItem v-if="!created">
					<Button type="primary" @click="formSubmit"></Button>
					<Button type="default" @click="formReset"></Button>
				</FormItem>
			</Form>
			</Col>
		</Row>
	</Card>
</template>

<script>
	import util from '@/libs/util.js';
	export default {
		data() {
			return {
				form: {
					username:"",
					password:"",
					realname:"",
					phone:"",
					email:"",
					sex:1
				},
				ruleValidate: {
					username: [
						{required: true,message: '',trigger: 'blur'},
						{type: 'string', max: 20, message: '20', trigger: 'blur'}
					],
					password: [
						{required: true,message: '',trigger: 'blur'},
						{type: 'string',min:5, max: 20, message: '5-20', trigger: 'blur'}
					],
					realname: [
						{required: true, message: '', trigger: 'blur'},
						{type: 'string', max: 20, message: '20', trigger: 'blur'}
					]
				},
				created:false
			}
		},
		computed: {
			userId: function() {
				return this.$route.query.userId;
			},
			title: function() {
				return this.$route.meta.title;
			}
		},
		methods: {
			formSubmit: function() {
				var self = this;
				self.$refs.form.validate((valid) => {
					if(valid) {
						var params = {
							userName: self.form.username,
							password: self.form.password,
							realName: self.form.realname,
							phone: self.form.phone,
							email: self.form.email,
							sex: self.form.sex
						}
						if(self.userId) {
							util.ajax.post("/manage/user/update/" + self.userId, util.params(params)).then(function(resp) {
								self.$Message.success('');
							}).catch(function(err) {
								self.$Message.error(',');
							});
						} else {
							util.ajax.post("/manage/user/create", util.params(params)).then(function(resp) {
								self.$Message.success('');
								self.created = true;
							}).catch(function(err) {
								self.$Message.error(',');
							});
						}

					}
				})
			},
			pageGoBack: function() {
				this.$router.go(-1);
			},
			formReset: function() {
				this.$refs.form.resetFields();
			},
			initData: function() {
				var self = this;
				if(self.userId) {
					util.ajax.get("/manage/user/get/" + self.userId).then(function(resp) {
						var result = resp.data.data;
						self.form.username = result.username;
						self.form.password = result.password;
						self.form.realname = result.realname;
						self.form.email = result.email;
						self.form.phone = result.phone;
						self.form.sex = result.sex;
						self.form.locked = result.locked;
					}).catch(function(err) {
						self.$Message.error(',');
					});
				}
			}
		},
		mounted: function() {
			this.initData();
		}
	}
</script>

<style>

</style>


