<template>
	<Card>
		<p slot="title">
			{{title}}
		</p>
		<div slot="extra">
			<a href="#" @click="pageGoBack">
				<Icon type="android-arrow-back"></Icon> </a>
		</div>
		<Row>
			<Col span="12" offset="6">
			<Form ref="form" :model="form" :label-width="80" :rules="ruleValidate">
				<FormItem label="" prop="name">
					<Input v-model="form.name" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="description">
					<Input v-model="form.description" placeholder=""></Input>
				</FormItem>
				<FormItem v-if="!created">
					<Button type="primary" @click="formSubmit"></Button>
					<Button type="default" @click="formReset"></Button>
				</FormItem>
			</Form>
			</Col>
		</Row>
	</Card>
</template>

<script>
	import util from '@/libs/util.js';
	export default {
		data() {
			return {
				form: {
					name: "",
					description: "",
				},
				ruleValidate: {
					name: [
						{required: true, message: '', trigger: 'blur'},
						{type: 'string',max: 20,message: '20',trigger: 'blur'}
					]
				},
				created: false
			}
		},
		computed: {
			organizationId: function() {
				return this.$route.query.organizationId;
			},
			title: function() {
				return this.$route.meta.title;
			}
		},
		methods: {
			formSubmit: function() {
				var self = this;
				self.$refs.form.validate((valid) => {
					if(valid) {
						var params = {
							name: self.form.name,
							description: self.form.description
						}
						if(self.organizationId) {
							util.ajax.post("/manage/organization/update/" + self.organizationId, util.params(params)).then(function(resp) {
								self.$Message.success('');
							}).catch(function(err) {
								self.$Message.error(',');
							});
						} else {
							util.ajax.post("/manage/organization/create", util.params(params)).then(function(resp) {
								self.$Message.success('');
								self.created = true;
							}).catch(function(err) {
								self.$Message.error(',');
							});
						}
					}
				})
			},
			pageGoBack: function() {
				this.$router.go(-1);
			},
			formReset: function() {
				this.$refs.form.resetFields();
			},
			initData: function() {
				var self = this;
				if(self.organizationId) {
					util.ajax.get("/manage/organization/get/" + self.organizationId).then(function(resp) {
						var result = resp.data.data;
						self.form.name = result.name;
						self.form.description = result.description;
					}).catch(function(err) {
						self.$Message.error(',');
					});
				}
			}
		},
		mounted: function() {
			this.initData();
		}
	}
</script>

<style>

</style>


