<template>
	<div>
		<Card>
			<p slot="title">
				<Icon type="help-buoy"></Icon> {{title}}
			</p>
			<div slot="extra">
				<a href="#" @click.prevent="doUserRoleUpdate" style="margin-right:15px;">
					<Icon type="ios-infinite"></Icon>
					
				</a>
				<a href="#" @click.prevent="doUserOrganUpdate" style="margin-right:15px;">
					<Icon type="ios-people"></Icon>
					
				</a>
				<a href="#" @click.prevent="goCreatePage">
					<Icon type="plus-round"></Icon>
					
				</a>
			</div>
			<LamboTable dataUrl="/manage/user/list" :columns="tableColumn" :searchParams="tableSearchParams" @on-selection-change="onSelectionChange">
				<div slot="search">
					<Input v-model="searchUserName" placeholder="" style="width: 200px" />
					<Button type="primary" icon="ios-search" @click="doSearch"></Button>
				</div>
			</LamboTable>
		</Card>
	</div>
</template>
<script>
	import util from '@/libs/util.js';
	import userOrganEdit from './userOrganEdit';
	import userRoleEdit from './userRoleEdit';
	//
	const editButton = (vm, h, currentRow, index) => {
		return h('Button', {
			props: {
				type: "primary",
				size: "small"
			},
			style: {
				margin: '0 5px'
			},
			on: {
				'click': () => {
					vm.goUpdatePage(currentRow.userId);
				}
			}
		}, '');
	};

	//
	const deleteButton = (vm, h, currentRow, index) => {
		return h('Button', {
			props: {
				type: "error",
				size: "small"
			},
			style: {
				margin: '0 5px'
			},
			on: {
				'click': () => {
					vm.doDelete(currentRow.userId);
				}
			}
		}, '');
	};
	export default {
		data() {
			return {
				searchUserName: "",
				tableSearchParams: {},
				idSelectedArr:[],
				userOrganModalOk:false,
				userRoleModalOk:false
			}
		},
		computed: {
			title: function() {
				return this.$route.meta.title;
			},
			tableColumn() {
				let columns = [];
				let self = this;

				columns.push({
					title: '#',
					key: 'userId',
					type:"selection",
					align:"center"
				});
				columns.push({
					title: '',
					key: 'username'
				});
				columns.push({
					title: '',
					key: 'realname'
				});
				columns.push({
					title: '',
					key: 'avatar',
					sortable: "custom"
				});
				columns.push({
					title: '',
					key: 'phone'
				});
				columns.push({
					title: '',
					key: 'email'
				});
				columns.push({
					title: '',
					key: 'sex',
					align: "center",
          enums: {
					  1:'',
            0:''
          },
					render: function(h, param) {
						return param.row.sex == 0 ? "" : "";
					}
				});
				columns.push({
					title: '',
					key: 'locked',
					align: "center",
          enums: {
            '1':'',
            '0':''
          },
					render: function(h, param) {
						return !param.row.locked ? "" : "";
					}
				});
				columns.push({
					title: '',
					key: 'permissionId',
					align: "center",
					fixed: "right",
					render: function(h, param) {
						return h('div', [
							editButton(self, h, param.row, param.index),
							deleteButton(self, h, param.row, param.index)
						]);
					}
				});
				return columns;
			}
		},
		methods: {
			doSearch: function() {
				this.tableSearchParams = {
					search: this.searchUserName
				}
			},
			goCreatePage: function() {
				this.$router.push({
					name: ''
				});
			},
			goUpdatePage: function(userId) {
				this.$router.push({
					name: '',
					query: {
						userId
					}
				});
			},
			doDelete: function(userId) {
				var self = this;
				this.$Modal.confirm({
					title: '',
					content: '<p>?</p>',
					onOk: () => {
						util.ajax.get("/manage/user/delete/" + userId).then(function(resp) {
							self.$Message.success('');
							self.doSearch();
						}).catch(function(err) {
							self.$Message.error(',');
						});
					}
				});
			},
			onSelectionChange:function(selection){
				var self = this;
				self.idSelectedArr = [];
				for(var item of selection){
					self.idSelectedArr.push(item.userId);
				}
			},
			doUserOrganUpdate: function(){
				var self = this;
				var idSelectedArr = self.idSelectedArr;
				if(idSelectedArr.length !== 1){
					self.$Modal.warning({
						title: '',
						content: '',
					});
				}else{
					self.userOrganModalOk = false;
					self.$Modal.confirm({
						title:"",
	                    render: (h) => {
	                        return h(userOrganEdit, {
	                            props: {
	                                userId: self.idSelectedArr[0],
	                                userOrganModalOk:self.userOrganModalOk
	                            }
	                        })
	                    },
                        onOk:function(){
                        	self.userOrganModalOk = true;
                        }
	                })
				}
			},
			doUserRoleUpdate:function(){
				var self = this;
				var idSelectedArr = self.idSelectedArr;
				if(idSelectedArr.length !== 1){
					self.$Modal.warning({
						title: '',
						content: '',
					});
				}else{
					self.userRoleModalOk = false;
					self.$Modal.confirm({
						title:"",
	                    render: (h) => {
	                        return h(userRoleEdit, {
	                            props: {
	                                userId: self.idSelectedArr[0],
	                                userRoleModalOk:self.userRoleModalOk
	                            }
	                        })
	                    },
                        onOk:function(){
                        	self.userRoleModalOk = true;
                        }
	                })
				}
			}
		}
	};
</script>



